.jupyter-wrapper {
    @import "jlab/index.scss";

    .jp-DownloadNB {
        // TODO
    }

    // Fix some margin/overflow issues
    .md-typeset__scrollwrap {
        margin: 0;
    }

    // Remove padding from Markdown cells
    .jp-MarkdownOutput {
        padding: 0;
    }

    // Anchor links hide/show
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        .anchor-link {
            display: none;
            margin-left: 10px;
        }

        &:hover {
            .anchor-link {
                display: inline-block;
            }
        }
    }

    // Markdown cells: Remove Input prompt
    .jp-Cell-inputWrapper .jp-InputPrompt {
        display: none;
    }

    // Code cells: Show Input prompt
    .jp-CodeCell .jp-Cell-inputWrapper .jp-InputPrompt {
        display: block;
    }

    // Highlighted pre cells
    .highlight {
        pre {
            overflow: auto;
        }
    }

    // Tags on cells
    // Mostly copying this from straight from Jupyter
    .celltoolbar {
        border: none;
        background: #eee;
        border-radius: 2px 2px 0px 0px;
        width: 100%;
        height: 29px;
        padding-right: 4px;
        box-orient: horizontal;
        box-align: stretch;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        box-pack: end;
        justify-content: flex-start;
        display: -webkit-flex;
    }

    .celltoolbar .tags_button_container {
        display: flex;
    }

    .celltoolbar .tags_button_container .tag-container {
        display: flex;
        flex-direction: row;
        flex-grow: 1;
        overflow: hidden;
        position: relative;
    }

    .celltoolbar .tags_button_container .tag-container .cell-tag {
        background-color: #fff;
        white-space: nowrap;
        margin: 3px 4px;
        padding: 0 4px;
        border-radius: 1px;
        border: 1px solid #ccc;
        box-shadow: none;
        width: inherit;
        font-size: 11px;
        font-family: "Roboto Mono", SFMono-Regular, Consolas, Menlo, monospace;
        height: 22px;
        /* line-height: 22px; */
        display: inline-block;
    }

    //
    .jp-InputArea-editor {
        width: 1px;
    }
    .jp-InputPrompt {
        overflow: unset;
    }
    .jp-OutputPrompt {
        overflow: unset;
    }

    .highlight-ipynb {
        overflow: auto;

        pre {
            margin: 0;
            padding: 5px 10px;
        }
    }

    // DataFrame
    // From: https://github.com/jupyter/notebook/blob/master/notebook/static/notebook/less/renderedhtml.less
    table.dataframe {
        margin-left: auto;
        margin-right: auto;
        border: none;
        border-collapse: collapse;
        border-spacing: 0;
        color: black;
        font-size: 12px;
        table-layout: fixed;
        thead {
            border-bottom: 1px solid black;
            vertical-align: bottom;
        }
        tr,
        th,
        td {
            text-align: right;
            vertical-align: middle;
            padding: 0.5em 0.5em;
            line-height: normal;
            white-space: normal;
            max-width: none;
            border: none;
        }
        th {
            font-weight: bold;
        }
        tbody tr:nth-child(odd) {
            background: #f5f5f5;
        }
        tbody tr:hover {
            background: rgba(66, 165, 245, 0.2);
        }
    }
    * + table {
        margin-top: 1em;
    }
}
